import React from "react";
import HelemtCanvas from "./canvas/Helemt2";

const Specs = () => {
  return (
    <div id="Product">
      <h1 style={{ color: "transparent" }}>RoboNari</h1>
      <h1>RoboNari</h1>
      <h2>Technical Specifications</h2>

      <div id="container">
      <div id="SpecsSection">
        <div>
          <div>
            <h3>MOBILITY</h3>
          </div>
        </div>
        <div>
          <div>
            <h3>
              <span>MATERIAL</span>
            </h3>
          </div>
        </div>
        <div>
          <div>
            <h3>FEATURE</h3>
          </div>
        </div>
        <div>
          <div>
            <h3>
              <span>DESIGN</span>
            </h3>
          </div>
        </div>
      </div>

      <div id="RoboNari">
        <HelemtCanvas />
      </div>

      </div>
      
      <div id="OtherSpecsDiv">
        <div id="left">
          <h3>5Kg</h3>
          <p>Body Weight</p>
        </div>
        <div id="center">
          <h3>20KMPH</h3>
          <p>Moving Speed</p>
        </div>
        <div id="right">
          <h3>30 Min</h3>
          <p>Charging Time</p>
        </div>
      </div>
    </div>
  );
};

export default Specs;
