import React, { useEffect , useMemo} from 'react';
// import '../App.css';

const ParticleAnimation = () => {
  const Particle = useMemo(() => {
  class Particle {
    constructor(canvas) {
      let random = Math.random();
    this.progress = 0;
    this.canvas = canvas;
    this.center = {
      x: window.innerWidth/2,
      y: window.innerHeight/2 };

    this.point_of_attraction = {
      // x: random,
      x: (window.innerWidth + window.innerWidth/4),
      y: (window.innerHeight / 4) };
      // y: random };




    if (Math.random() > 0.5) {
      this.x = window.innerWidth * Math.random();
      this.y = Math.random() > 0.5 ? -Math.random() - 100 : window.innerHeight + Math.random() + 100;
    } else {
      this.x = Math.random() > 0.5 ?window.innerHeight + Math.random() + 100: -Math.random() - 100;
      this.y = window.innerWidth * Math.random();

    }

    this.s = Math.random() * 2;
    this.a = 0;
    this.w = window.innerWidth;
    this.h = window.innerHeight;
    this.radius = random > .2 ? Math.random() * 1 : Math.random() * 3;
    this.color = random > .2 ? "#B1B9FF" : "#6776FF";
    this.radius = random > .8 ? Math.random() * 2.2 : this.radius;
    this.color = random > .8 ? "#A500CE" : this.color;
    }
  
    calculateDistance(v1, v2) {
      let x = Math.abs(v1.x - v2.x);
    let y = Math.abs(v1.y - v2.y);
    return Math.sqrt(x * x + y * y);
    }
  
    render() {
      this.canvas.beginPath();
    this.canvas.arc(this.x, this.y, this.radius, 0, 2 * Math.PI);
    this.canvas.lineWidth = 2;
    this.canvas.fillStyle = this.color;
    this.canvas.fill();
    this.canvas.closePath();
    }
  
    move() {
      let p1 = {
        x: this.x,
        y: this.y
      };
  
      let distance = this.calculateDistance(p1, this.point_of_attraction);
      let force = Math.max(100, 1 + distance);
  
      let attr_x = (this.point_of_attraction.x - this.x) / force;
      let attr_y = (this.point_of_attraction.y - this.y) / force;
  
      this.x += Math.cos(this.a) * this.s + attr_x;
      this.y += Math.sin(this.a) * this.s + attr_y;
      this.a += Math.random() > 0.5 ? Math.random() * 0.9 - 0.45 : Math.random() * 0.4 - 0.2;
  
      if (distance < 30 + Math.random() * 100) {
        return false;
      }
  
      this.render();
      this.progress++;
      return true;
    }
  
  }

  return Particle;
}, []);

  useEffect(() => {
    const max_particles = 800;
    
    let particles = [];
    const frequency = 0.01;
    const init_num = max_particles;
    const max_time = frequency * max_particles;
    let time_to_recreate = false;

    // Enable repopolate
    setTimeout(function () {
      time_to_recreate = true;
    }, max_time);

    function createPopolateFunction(i) {
      return function () {
        particles.push(new Particle(canvas));
      };
    }
    
    function popolate(num) {
      for (let i = 0; i < num; i++) {
        setTimeout(createPopolateFunction(i), frequency * i);
      }
      return particles.length;
    }

    // Create the canvas
    const tela = document.createElement('canvas');
    tela.width = window.innerWidth;
    tela.height = window.innerHeight;
    tela.style.marginTop = '-120vh';
    tela.style.maxWidth = '100vw';
    // tela.style.transform = 'scale(0.8) translate(100px,100px)';
    tela.style.background = 'transparent';
    // tela.style.maxHeight = '800px';
    // tela.style.transform = 'rotate(-180deg)';
    document.body.appendChild(tela);

    const canvas = tela.getContext('2d');

    // Animation loop
    function clear() {
      canvas.globalAlpha = 0.25;
      canvas.fillStyle = 'rgb(0,0,30)';
      canvas.fillRect(0, 0, tela.width, tela.height);
      canvas.globalAlpha = 0.8;
    }

    function update() {
      particles = particles.filter(function (p) { return p.move(); });
      if (time_to_recreate) {
        if (particles.length < init_num) {
          popolate(1);
          // console.log('Ricreo');
        }
      }
      clear();
      requestAnimationFrame(update.bind(this));
    }

    update(); // Start the animation loop 
    }, [Particle]); // Empty dependency array to ensure the effect runs only once

  return <div></div>;
};

export default ParticleAnimation;

