import "../App.css";
import React from "react";
import { Logo, HeroBg } from "../assets";
import { NavLink } from "react-router-dom";
import HelemtCanvas from "./canvas/Helemt1";
import { Link } from "react-scroll";

const Hero = () => {
  const [scrolled, setScrolled] = React.useState(false);
  const [Ohovered, setOHovered] = React.useState(false);
  const [Phovered, setPHovered] = React.useState(false);
  const [Rhovered, setRHovered] = React.useState(false);
  const [Chovered, setCHovered] = React.useState(false);
  const [Shovered, setSHovered] = React.useState(false);
  const [Ghovered, setGHovered] = React.useState(false);
  const [isClicked , setClicked] = React.useState(false);

  // React.useEffect(()=>{
    
  //     document.getElementById('menuIcon').addEventListener('click',setClicked(true)); 
  // });

  React.useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY;
      if (scrollTop > 100) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    // <>
    // <BrowserRouter>
    <div className="Hero" id="Hero">
      {/* Hero */}
      <nav className={`${scrolled ? "scrollNav nav" : "nav"}`}>
        <NavLink
          to="/"
          onClick={() => {
            window.scrollTo(0, 0);
          }}
          smooth={true}
          duration={500}
        >
          <img id="Logo" src={Logo} alt="Logo" />
        </NavLink>
        {/* <div id="navContainer"> */}
        <ul id={`${isClicked ? "responsiveNav" : ""}`}>
          <Link to="Overview" smooth={true} duration={400}>
            <li
              onMouseEnter={() => setOHovered(true)}
              onMouseLeave={() => setOHovered(false)}
            >
              Overview
            </li>
            <div
              className={`${
                Ohovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </Link>
          <Link to="Product" smooth={true} duration={400}>
            <li
              onMouseEnter={() => setPHovered(true)}
              onMouseLeave={() => setPHovered(false)}
            >
              Product
            </li>
            <div
              className={`${
                Phovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </Link>
          <Link to="Reviews" smooth={true} duration={600}>
            <li
              onMouseEnter={() => setRHovered(true)}
              onMouseLeave={() => setRHovered(false)}
            >
              Reviews
            </li>
            <div
              className={`${
                Rhovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </Link>
          <Link className="tabletView" to="Contact" smooth={true} duration={600}>
            <li
              onMouseEnter={() => setCHovered(true)}
              onMouseLeave={() => setCHovered(false)}
            >
              Contact Us
            </li>
            <div
              className={`${
                Chovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </Link>
          <NavLink className="mobileView" to="/Gallery" smooth={true} duration={600}>
            <li
              onMouseEnter={() => setGHovered(true)}
              onMouseLeave={() => setGHovered(false)}
            >
              Gallery
            </li>
            <div
              className={`${
                Ghovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </NavLink>
          <NavLink className="mobileView" to="/SuccessStories" smooth={true} duration={600}>
            <li
              onMouseEnter={() => setSHovered(true)}
              onMouseLeave={() => setSHovered(false)}
            >
              Success Stories
            </li>
            <div
              className={`${
                Shovered ? "borderLine borderLineHovered" : "borderLine"
              }`}
            ></div>
          </NavLink>
        </ul>
        
        {/* </a> */}

        <div id="menuIcon" className={`${ isClicked ? "container change" : "container"}`} onClick={()=>{
          
          if(isClicked) setClicked(false)
          else setClicked(true);
          }} >
          <div className="bar1"></div>
          <div className="bar2"></div>
          <div className="bar3"></div>
        </div>
        {/* </div> */}
        <Link to="Contact" id="contactButtonLink" smooth={true} duration={1}>
          {/* <a href="/#Contact"> */}
          <div id="contactButton">Contact Us</div>
        </Link>
      </nav>
      <div id="bg-circle"></div>
      <div className="HeroSection1">
        <div>
          <div className="HeroHeadText">
            <h1>
              Discover New Era Of Marketing With <span>RoboNari</span>
            </h1>
          </div>
          <div className="HeroSubText">
            <h2>India's First Female Marketing Robot</h2>
          </div>
        </div>
        <div id="HeroNari" style={{ width: "350px", height: "300px", marginTop: "50px" }}>
          <HelemtCanvas />
        </div>
      </div>
      <div className="HeroSection2">
        <div className="callToAction">
          <NavLink to="/Gallery">
            <div id="btn1">Visit Gallery</div>
          </NavLink>
          <NavLink to="/SuccessStories">
            <div id="btn2">Success Stories</div>
          </NavLink>
        </div>
        <div id="spacediv" style={{ width: "300px", height: "50px" }}></div>
      </div>
      <img id="HeroBg" src={HeroBg} alt="HeroBg" />
    </div>
  );

};

export default Hero;
