import ParticleAnimation from '../components/canvas'
const Contact = () =>{

    return(
    <div id="Contact">
        <div id="particleWrapper">
    <ParticleAnimation/>
    </div>
        <div id="wrapper">
        <h1><span>Market it</span> The Best With <span style={{color:"transparent"}}>RoboNari</span></h1>
        <form>
            <div>
                <div id='container'>
            <h1>Get In Touch</h1>
            <h2>Contact US</h2>
            </div>
            </div>
            <label htmlFor="name">Your Name
            <input type="text" id="name" placeholder="What's your good name?"/>
            </label>
            <label htmlFor="email">Your Email
            <input type="email" id="email" placeholder="What's your web address?"/>
            </label>
            <label htmlFor="message">Your Message
            <textarea type="email" id="message" placeholder="What do you want to inquire" rows={10} cols={30}></textarea>
            </label>
            <button type="submit" id="btn">Send</button>
        </form>
        </div>
    </div>
    );

};


export default Contact;